const initialState = {
    searchResultsCount:0,
    links:[{
      "path":"/home",
      "id":"HOME",
      "display":"â¿³"
    },{
      "path":"/users",
      "id":"USERS",
      "display":"Users"
    }]
};

const reducer = (state = initialState, action) => {
  
  console.log("reducing: ")
  console.log(action);
  console.log(state);
  console.log("+++++++++++++++++")
  
  if(action.type === 'STORE_SEARCH_RESULTS_COUNT') {
    let newState = {...state};
    newState.searchResultsCount = action.val;
    return newState;
  }
  return state;
};


export default reducer;