import React from 'react';
import './Header.scss';
import Button from '@material-ui/core/Button';
import Avatar from '@material-ui/core/Avatar';
import Popover from '@material-ui/core/Popover';
import TextField from '@material-ui/core/TextField';
import styled, { ThemeProvider } from "styled-components";
import axios from 'axios';
import { withRouter, Link } from 'react-router-dom'
import { connect } from 'react-redux';
import { compose } from 'redux';


interface State {
  openLoginMenu:boolean,
  anchorEl:any,
  headerVisible:boolean
}

const mapStateToProps = state => {
  return {
    searchResultsCount: state.searchResultsCount,
    links: state.links
  };
};

const mapDispatchToProps = dispatch => {
  return {
    recordSearchResultsTotal: (d) => dispatch({type:'STORE_SEARCH_RESULTS_COUNT', val:d})
  };
};

class Header extends React.Component {
  state:State;
  private noHeaders = ["/"];
  
  constructor(public props) {
    super(props);
    this.state = {
      openLoginMenu:false,
      anchorEl:null,
      headerVisible:false
    };
  }
  
  componentDidMount() {
    this.unlisten = this.props.history.listen((location, action) => {
      console.log("on route change");
      this.setHeaderVisibility();
    });
    
    this.setHeaderVisibility();
    console.log("---------");
    console.log(this.props);
  }
  
  setHeaderVisibility() {
    let path = this.props.location.pathname;
    
    if(this.noHeaders.indexOf(path) === -1)
      this.setState({headerVisible:true});
    else
      this.setState({headerVisible:false});
  }
  
  logout() {
    localStorage.clear();
    window.location.href = "/";
  }
  
  unlisten() {
    
  }
  
  changePageProgramatically(e) {
    e.preventDefault();
    this.props.history.push("/");
  }
  
  openPopover(el) {
    this.setState({
      anchorEl:el,
      openLoginMenu:true
    });
  }
  
  render() {
    
    return (
        <header className={'mainHeader ' +(this.state.headerVisible ? " " : "hidden ")}>
          <div className="p-3">
            <div className="row align-items-center">
              <div className="col-md-12">
                <div className="row">
                  <nav className="col-auto linkWrapper">
                    <ul className="nav-pills pl-2 m-0">
                      {this.props.links && this.props.links.map((item,index)=>{
                        return <li className="nav-item pr-3 mb-1 mt-1" key={index}>
                          <Link to={item.path}><button type="button" className="btn btn-outline-dark">{item.display}</button></Link>
                        </li>
                      })}
                    </ul>
                  </nav>
                  <div className="text-right flex-grow-1 px-3">
                    <div className="avatarWrapper" onClick={(event)=>this.openPopover(event.currentTarget)}>
                      <Avatar className="avatarButton" src="/public/user.png" />
                    </div>
                    <Popover
                      anchorEl={this.state.anchorEl}
                      open={this.state.openLoginMenu}
                      onClose={(event)=>{this.setState({openLoginMenu:false,anchorEl:null});}}
                      anchorOrigin={{
                        vertical: 'bottom',
                        horizontal: 'center',
                      }}
                      transformOrigin={{
                        vertical: 'top',
                        horizontal: 'center',
                      }}
                    >
                    <div className="list-group">
                      <div onClick={(event)=>this.logout()} className="list-group-item list-group-item-action list-group-item-light">Logout</div>
                    </div>
                    </Popover>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </header>
    );
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(withRouter(Header));